#mainNavbar {
    /* border-top: 6px solid #F15025; */
    border-top: 6px solid#ffc299 !important;
    /* background-color: #242a33; */
    background-color: #662900;
    padding: 0 !important;
    margin: 0 !important;
    width: 100% !important;
}

#mainNavbar .navbar-brand {
    display: block !important;
    background-color: #ffc299;
    padding: 5px 30px !important;
    font-style: italic;
    color: #f5f3f5;
}

#mainNavbar .nav-link {
    color: white;
    font-size: 20px;
    padding: 10px 22px !important;
    margin: 0 10px !important;
    margin-top: 5px !important;
}

#mainNavbar .nav-link:hover {
    color: #f5f3f5;
    background-color: rgb(206, 77, 17);
    border-radius: 0px;
}

#mainNavbar .subColor {
    color: black !important;
}

#mainNavbar .subColor.nav-link:hover {
    color: white !important;
    text-decoration: none !important;
}

#mainNavbar .dropdown-item {
    width: 350px !important;
}

@media (min-width:769px) {
    #mainNavbar ul .dropdown:hover>.dropdown-menu {
        display: block;
    }
    #mainNavbar ul .dropdown>.dropdown-menu {
        display: none;
    }
}

#mainNavbar .imgSize {
    width: 250px;
}


